<script>
import tableComponent from "@/components/Table/Table.vue";
//import UploadComponent from "@/components/Upload/Upload.vue";
import config from "../../../public/config/customSetting.json";

export default {
  name: "idCardApply-component",
  components: { tableComponent },
  data() {
    return {
      dialogVisible: false,
      config: config,
      fileIndex: 0,
      selectedItem: [],
      courseNo: "",
      courseId: "",
      form: {
        course_Id: "",
        applyPeople: [],
      },
      rules: {
        course_Id: [{ required: true, message: "必填", trigger: "change" }],
        applyPeople: [{ required: true, message: "必填", trigger: "change" }],
      },
      cancelTableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "col1",
          label: "姓名 (性別) 身分證字號",
          width: "180",
        },
        //{
        //  id: "signUpStatus",
        //  label: "狀態",
        //  width: "125",
        //},
        {
          id: "col3",
          label: "申請日期 (申請原因)",
          width: "200",
        },
        {
          id: "col4",
          label: "承攬商入廠施工群組 ",
          width: "300",
        },
        {
          id: "col5",
          label: "審查狀態(總務) ",
          width: "150",
        },
      ],
      course1Data: [],
      course2Data: [],
      coursePeople: [],
      course1List: [],
      CancelSignUp4CardData: [],
      cancelIdSignUpList: [],
      cancelSignUpPage: 1,
      courseListPage: 1,
    };
  },
  computed: {},
  methods: {
    async getData() {
      const courseListRes = await this.$store.dispatch("qryVenCourseList");
      const courseListData = courseListRes.data.data;
      this.course1Data = JSON.parse(JSON.stringify(courseListData.course1));
      this.course1Data = this.course1ListColumns(this.course1Data);
      this.course1List = this.course1Data.slice(0, 10);
      const cancelSignUpRes = await this.$store.dispatch("cancelIdSignUpList");
      const signUpData = cancelSignUpRes.data.data;
      this.CancelSignUp4CardData = JSON.parse(JSON.stringify(signUpData));
      this.CancelSignUp4CardData = this.cancelSignUpListColumns(
        this.CancelSignUp4CardData
      );
      this.cancelIdSignUpList = this.CancelSignUp4CardData.slice(0, 10);
    },
    cancelSignUpChange(page) {
      this.cancelSignUpPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.cancelIdSignUpList = this.CancelSignUp4CardData.slice(cur, last);
    },
    courseListChange(page) {
      this.courseListPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.course1List = this.course1Data.slice(cur, last);
    },
    beginDateHandler(evt, index) {
      console.log("people." + index);
      if (evt) {
        this.$refs.form.clearValidate("people." + index);
      } else {
        this.$refs.form.validateField("people." + index);
      }
    },
    fileChangeHandler(file) {
      this.form.people.forEach(async (e, index) => {
        if (index === this.fileIndex) {
          e.fileName = file.name;
          e.fileBase64 = await this.toBase64(file.raw);
          e.uid = file.uid;
        }
      });
      this.$refs.form.clearValidate("people." + this.fileIndex + ".fileName");
    },
    courseIdChangeHandler(item) {
      const courseId = item.course_Id;
      this.courseId = item.course_Id;
      //this.scheduleNo = item.schedule_No;
      //this.form.class_Date = item.class_Date;
      //this.form.available_Begin_Date = item.available_Begin_Date;
      this.$store.dispatch("qryIdChangePeople", courseId).then((res) => {
        const data = res.data.data;
        this.coursePeople = data;
        this.coursePeople.forEach((e) => (e.label = `${e.P_Name}(${e.id})`));
      });
      this.form.people = [];
      this.form.applyPeople = [];
    },
    closeDialog() {
      this.form.schedule_No = "";
      this.dialogVisible = false;
      this.psdInput = "";
    },
    peopleChangeHandler(item) {
      this.form.people = item;
    },
    selectionChangeHandler(val) {
      this.selectedItem = val;
    },
    canSelectRow(row) {
      //判斷是否可取消申請
      if (row.approve_Status == "審查中") {
        return true;
      } else {
        return false;
      }
    },
    updCancelIdSignUp() {
      this.$store
        .dispatch("updCancelIdSignUp", this.selectedItem)
        .then((res) => {
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "修改成功",
            });
            this.cancelSignUpPage = 1;
            this.getData();
            this.$refs.cancelMultipleTable.$refs.multipleTable.clearSelection();
          }
        });
    },
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.form.course_Id = this.courseId;
          //console.log(this.form.course_Id);
          this.$store.dispatch("saveApplyPeople", this.form).then((res) => {
            if (res.data.isOK) {
              this.$message({
                type: "success",
                duration: "5000",
                showClose: true,
                message: "送出成功",
              });
              this.resetForm(formName);
              this.getData();
            }
          });
        } else {
          this.$message({
            type: "error",
            duration: "5000",
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.form.people = [];
    },
    course1ListColumns(formData) {
      formData.forEach((e, index) => {
        e.index = index + 1;
        e.label = `${e.course_Name}`;
      });
      return formData;
    },
    cancelSignUpListColumns(formData) {
      formData.forEach((e, index) => {
        e.index = index + 1;
        e.col1 = `${e.p_Name}/${e.sex}/${e.id}`;
        e.col3 = `${e.apply_Date} (遺失)`;
        e.col4 = `${e.course_Name}`;
        e.col5 = `${e.approve_Status}`;
        e.statusClass = e.approve_Status === "拒絕" ? "rejected" : "";
      });
      return formData;
    },
  },
  mounted() {
    this.getData();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./idCardApply.html"></template>
<style lang="sass" scoped src="./idCardApply.sass"></style>
